.popup-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: rgba(0,0,0,0.4);
  transform: translateY(-100%);
  transition: transform 0.3s;
}

.is-showed {
  transform: translateY(0);
}

.popup {
  background: #fff;
}

.table td {
  max-width: 110px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}